<template>

  <li class="flex-col child-list">

    <div class="card card-little flex-align" :class="cr.job !== 0 ? 'card-recipe':''">
      <div class="flex-align">
        <img class="img-small" :src="'assets/items/' + cr.img + '.png'">
        <div class="flex-col info">
          <span style="font-size:0.85rem">{{ cr.name[crafts.lang] }} x{{cr.qt}}</span>
          <span style="font-size:0.85rem" v-if="cr.job !== 0">
            {{ crafts.jobs.find( x => x.id === cr.job ).name[crafts.lang]}}
            Nv. {{ cr.lvl }} 
          </span>
        </div>
      </div>

      <div class="flex-align">
        <i class="fas fa-swatchbook" v-if="cr.job !== 0 && cr.ingredients.length > 1"  @click.self.stop="upShow(cr)"></i>
      </div>
    </div>

    <ul class="" v-if="cr.job !== 0">
    <transition-group name="list" tag="ul">
        <formcraft :cr='cr' v-for="cr in cr.ingredients[cr.show]" :key='cr.id'></formcraft>
    </transition-group>
    </ul>
    
  </li>

</template>

<script>
  import crafts from './Store'
  import formcraft from './Form'

  export default {
    name:'formcraft',

    components: { formcraft },

    data() {
      return {
        crafts: crafts.state
      }
    },

    props: ['cr'],

    methods: {
      upShow(cr) {
        if (cr.show + 1 === cr.ingredients.length) cr.show = 0
        else cr.show++
      }
    }

  }
</script>


