class Ingredient {

  constructor(ingredient, qt) {
    this.qtBase = qt
    this.qt = qt
    this.id = ingredient.id
    this.name = ingredient.name
    this.img = ingredient.img
    this.rarity = ingredient.rarity
    this.job = 0
  }

  addQt(qt) {
    this.qt += qt
  }
}

export default Ingredient


