<template>
  <section id="search">

    <div class="section-head">
      <h2>{{crafts.txt.search[crafts.lang]}}</h2>
    </div>

    <div class="section-body">

      <div class="flex-align body-top">

        <div class="flex-align select-jobs" :style="drop? 'border: 2px solid #53C4B8':''" @click="drop = !drop">
          <span>{{ crafts.jobs.find( x => x.id === currentJob).name[crafts.lang] }}</span>
          <i class="fas fa-chevron-circle-down" :style="drop?'color:#53C4B8':'transform:rotate(-0.25turn)'"></i>

          <div class="option-jobs" v-if="drop">
            <div class="job flex-align" v-for="c in crafts.jobs.filter(x => x.id !== currentJob)" :key="c.id" @click.stop.prevent="changeJob(c.id)"> 
              {{ c.name[crafts.lang] }}
              <img class="img-small" :src="'assets/jobs/' + c.id + '.png'">
            </div>
          </div>
        </div>


        <input :placeholder="crafts.txt.craftsMsg[crafts.lang]" type="text" v-model="search">

      </div>
      <transition-group name="list" tag="div" appear>
      <div class="card flex-align list-item" :key="c.id" v-if="!crafts.list.find(x => x.id === c.id)" v-for="c in getResults">

        <div class="flex-align flex-left">
          <img class="img-big" :src="'assets/items/' + c.img + '.png'">
          <div class="flex-col info">
            <span>{{ c.name[crafts.lang] }}</span>
            <span>
              Niv. {{ c.lvl }} - {{ crafts.rarity.find( x => x.id === c.rarity).name[crafts.lang] }}
            </span>
          </div>
        </div>

        <div class="flex-right">
          <i class="fa fa-shopping-cart" @click.stop.prevent="addCraft(c)"></i>
        </div>

      </div>
      </transition-group>
      <div class="list-item" style="margin-top:15px;">{{ checkResults }}</div>

    </div> 

  </section>
</template>

<script>

  import crafts from './Store'
  import Craft from './Craft'

  export default {

    data() {
      return {
        crafts: crafts.state,
        search:'',
        currentJob: 71,
        drop: false
      }
    },

    computed: {
      getResults() {
        let result = []
        result = data.filter(x => x.job === this.currentJob && x.name[this.crafts.lang].toLowerCase().indexOf(this.search.toLowerCase()) !== -1)
        return result.sort(function (a, b) { return a.lvl - b.lvl })
      },

      checkResults() {
        var result = this.getResults.length
        if (result !== 0) {
          this.getResults.forEach( (el) => {
            let craft = this.crafts.list.find( x => x.id === el.id)
            if (craft !== undefined) result -= 1
          })
        }
        if (result < 1) return "Aucun résultat."
        return result + ' résultat'+ (result > 1 ? "s":"")
      }
    },

    methods: {
      addCraft(craft) {
        this.crafts.list.push(new Craft(craft, 1))
      },

      changeJob(id) {
        this.currentJob = id,
        this.drop = false
      }
    }

  }

</script>


