<template>
  <div id="container">

    <header class="flex-align" style="justify-content:space-between;" >
      <div>
      </div>
      <div class="lang" :class="[crafts.lang === 0? 'lang-en': 'lang-fr']" @click.prevent.stop.self="crafts.lang = (crafts.lang === 0?1:0)">

      </div>
    </header>

    <div class="flex-align c-app">
      <search></search>
      <quantity></quantity>
      <shop></shop>
    </div>
  </div>
</template>

<script>

  import search from './Search'
  import quantity from './Quantity'
  import shop from './Shop'
  import crafts from './Store'

  export default {

    data() {
      return {
        crafts: crafts.state
      }
    },

    components: { search, quantity, shop },
    
  }
</script>


