<template>
  <section id="shop">

    <div class="section-head">
      <h2>{{crafts.txt.shopping[crafts.lang]}}</h2>
    </div>

    <div class="section-body">

      <div class="flex-align body-top">
        <div>
          {{crafts.list.length === 0 ? crafts.txt.noCraft[crafts.lang] : getQt + " " + crafts.txt.resources[crafts.lang] +":"}}
        </div>

        <div>
          <a :href="dlCsv" download="shopping.csv" v-if="crafts.list.length > 0" class="fas fa-file-excel" ></a>
          <a :href="dlTxt" download="shopping.txt" v-if="crafts.list.length > 0" class="fas fa-file-alt" ></a>
        </div>
      </div>

      <transition-group name="list" tag="div">
      <div class="card flex-align list-item" :key="c.id" v-for="c in getShop">

        <div class="flex-align flex">
          <img class="img-big" :src="'assets/items/' + c.img + '.png'">
          <div class="flex-col info">
            <span>{{ c.name[crafts.lang] }} x{{c.qt}}</span>
            <span>
              {{ crafts.rarity.find( x => x.id === c.rarity ).name[crafts.lang]}} 
            </span>
          </div>
        </div>

        <a :href="'https://www.wakfu.com/'+ (crafts.lang === 0 ? 'fr':'en') +'/mmorpg/'+ (crafts.lang === 0 ? 'encyclopedie/ressources' : 'encyclopedia/resources') +'/' + c.id + '-wakfu'" target="_blank" class="fas fa-link">
        </a>
      </div>
      </transition-group>
    </div>
  </section>
</template>

<script>

  import crafts from './Store'

  export default {
    data() {
      return {
        crafts: crafts.state
      }
    },
    
    computed: {
      getShop () {
        let arr = []
        this.crafts.list.forEach((craft) => {
          crafts.getIngs(craft, arr)
        })
        
        return arr
      },

      getQt () {
        var allQt = 0
        this.getShop.forEach( (el) => {
          allQt += el.qt
        })
        return allQt
      },

      dlTxt () {
        let stringify = this.crafts.txt.materials[this.crafts.lang] + ":\n\n"
        this.getShop.forEach( (el) => {
          stringify += "- " + el.name[this.crafts.lang] + " x" + el.qt + "\n"
        })
        stringify += "\n" + this.crafts.txt.step[this.crafts.lang] +":\n\n"
        this.crafts.list.forEach( (el) => {
          stringify += el.name[this.crafts.lang] + ' x' + el.qt + ' (' + this.crafts.jobs.find( x => x.id === el.job).name[this.crafts.lang] + ' Niv. ' + el.lvl + ')\n'
          stringify += this.getStep(el, 1)
        })
        let data = new Blob([stringify], {type: 'text/plain'});
        return window.URL.createObjectURL(data);
      },

      dlCsv () {
        let stringify = this.crafts.txt.csv[this.crafts.lang] +"\n"
        this.getShop.forEach( (el) => {
          stringify += el.name[this.crafts.lang] + ',' + el.qt + ',' + this.crafts.rarity.find( x => x.id === el.rarity).name[this.crafts.lang] + '\n'
        })
        let data = new Blob([stringify], {type: 'text/plain'});
        return window.URL.createObjectURL(data)
      }
    },

    methods: {

      getTab(tab) {
        let out = ''
        for (let i = 0; i < tab; i++) {
          out += '\t'
        }
        return out
      },

      getStep (c, tab) {
        var out = ''
        c.ingredients[c.show].forEach( (el) => {
          out += this.getTab(tab) + ' ' + el.name[this.crafts.lang] + ' x' + el.qt
          if (el.job === 0) out += '\n'
          else {
            out += ' ( ' + this.crafts.jobs.find(x => x.id === el.job).name[this.crafts.lang] + ' Niv.' + el.lvl + ')\n'
            out += this.getStep(el, tab + 1)
          }
        })
        return out
      },

    }
  }

</script>


