import Ingredient from './Ingredient.js'

class Store {
  constructor () {
    this.state = {
      lang:0,
      list: [],
      jobs: [
        { id: 71, name: ['Forestier', 'Lumberjack'] },
        { id: 72, name: ['Herboriste', 'Herbalist'] },
        { id: 73, name: ['Mineur', 'Miner'] },
        { id: 64, name: ['Paysan', 'Farmer'] },
        { id: 75, name: ['Pêcheur', 'Fisherman'] },
        { id: 74, name: ['Trappeur', 'Trapper'] },
        { id: 77, name: ['Armurier', 'Armorer'] },
        { id: 78, name: ['Bijoutier', 'Jeweler'] },
        { id: 40, name: ['Boulanger', 'Baker'] },
        { id: 76, name: ['Cuisinier', 'Chef'] },
        { id: 81, name: ['Ebéniste', 'Handyman'] },
        { id: 83, name: ['Maitre d\'Armes', 'Weapons Master'] },
        { id: 80, name: ['Maroquinier', 'Leather Dealer'] },
        { id: 79, name: ['Tailleur', 'Tailor'] },
      ],
      rarity: [
        { id: 0, name: ['Commun', 'Common'] },
        { id: 1, name: ['Inhabituel', 'Unusual'] },
        { id: 2, name: ['Rare', 'Rare' ] },
        { id: 3, name: ['Mythique', 'Mythical' ] },
        { id: 4, name: ['Légendaire', 'Legendary' ] },
        { id: 5, name: ['Relique', 'Relic'] },
        { id: 6, name: ['Souvenir', 'Souvenir'] },
        { id: 7, name: ['Epique', 'Epique'] }
      ],
      txt: {
        search:['Recherche', 'Search'],
        craftsMsg:['On s\'craft quoi ?', 'Give me ALL the crafts!'],
        stock:['Stock', 'Stock'],
        shopping: ['Courses', 'Shopping'],
        noCraft: ['Aucun craft...', 'No craft...'],
        resources:['ressources', 'resources'],
        step: ['Décomposition', 'Step By Step'],
        materials: ['Ingrédients', 'Ingredients'],
        csv: ['Nom,Quantité,Rareté', 'Name, Quantity, Rarity']
      }
    }
  }

  getIngs(craft, arr) {
    craft.ingredients[craft.show].forEach((ing) => {
      if (ing.job === 0) {
        let checkIng = arr.find( x => x.id === ing.id)
        if (checkIng === undefined) arr.push(new Ingredient(ing, ing.qt))
        else checkIng.qt += ing.qt
      } else {
        this.getIngs(ing, arr)
      }
    })
  }
}

export default new Store()


