<template>
  <section id="quantity">

    <div class="section-head">
      <h2>{{crafts.txt.stock[crafts.lang]}}</h2>
    </div>

    <div class="section-body">

      <div class="flex-align body-top">
        <div>
          {{crafts.list.length === 0 ? crafts.txt.noCraft[crafts.lang]: crafts.list.length + " craft"+ (crafts.list.length > 1 ?"s":"") + ":"}}
        </div>

        <div v-if="crafts.list.length > 0">
          <i class="fas fa-caret-square-down" :style="visibleCrafts.length > 0 ? 'transform:rotate(0turn)':'transform:rotate(-0.25turn)'" @click.stop.self="visibilityAll()"></i>
          <i class="fas fa-eraser" @click.stop.self="removeAll()"></i>
        </div>
      </div>
      <transition-group name="list" tag="div">
      <div class="list-item" :key="c.id" v-for="c in crafts.list">

        <div class="card card-recipe flex-align">
          <div class="flex-align">
            <img class="img-big" :src="'assets/items/' + c.img + '.png'">
            <div class="flex-col info">
              <span>{{ c.name[crafts.lang] }}</span>
              <span>
                {{ crafts.jobs.find( x => x.id === c.job ).name[crafts.lang]}}
                Nv. {{ c.lvl }} 
              </span>
            </div>
          </div>

          <div class="flex-align">
            <i class="fas fa-swatchbook" v-if="c.ingredients.length > 1"  @click.self.stop="upShow(c)"></i>
            <input type="number" value="1" min="1" max="500" @change="changeQt(c, $event)">
            <i class="fas fa-chevron-circle-down" :style="visibleCrafts.includes(c.id) ? 'transform:rotate(0turn)':'transform:rotate(-0.25turn)'" @click.self.prevent="setVisibility(c.id)"></i>
            <i class="fas fa-times-circle" @click.self.stop="removeCraft(c.id)"></i>
          </div>
        </div>

        <ul v-if="visibleCrafts.includes(c.id)">
          <formcraft :cr="c" v-for="c in c.ingredients[c.show]" :key="c.id"></formcraft>
        </ul>

      </div>
      </transition-group>
    </div>

  </section>
</template>

<script>

  import crafts from './Store'
  import formcraft from './Form'

  export default {
    data() {
      return {
        crafts: crafts.state,
        visibleCrafts: [],
      }
    },

    components: { formcraft },

    methods: {
      removeCraft(id) {
        this.crafts.list = this.crafts.list.filter( x => x.id !== id)
        this.visibleCrafts = this.visibleCrafts.filter( x => x !== id)
      },
      removeAll() {
        this.crafts.list = []
        this.visibleCrafts = []
      },
      changeQt(c, $event) {
        if (+$event.target.value < 1 && +$event.target.value > 500) $event.target.value = 1
        let item = this.crafts.list.find( x => x.id === c.id).changeQt($event.target.value)
      },
      setVisibility(id) {
        if (this.visibleCrafts.includes(id)) {
          this.visibleCrafts = this.visibleCrafts.filter( x => x !== id)
        } else {
          this.visibleCrafts.push(id)
        }
      },
      visibilityAll() {
        if (this.visibleCrafts.length > 0) this.visibleCrafts = []
        else {
          this.crafts.list.forEach( (el) => {
            this.visibleCrafts.push(el.id)
          })
        }
      },

      upShow(cr) {
        if (cr.show + 1 === cr.ingredients.length) cr.show = 0
        else cr.show++
      }
    }
  }

</script>


